import { basePath } from "./router.js";
// loadComponents.js
async function loadComponent(elementId, componentPath) {
  try {
    // Ð¤Ð¾Ñ€Ð¼Ð¸Ñ€ÑƒÐµÐ¼ URL Ñ ÑƒÑ‡Ñ‘Ñ‚Ð¾Ð¼ Ñ†ÐµÐ½Ñ‚Ñ€Ð°Ð»Ð¸Ð·Ð¾Ð²Ð°Ð½Ð½Ð¾Ð³Ð¾ basePath
    const url = componentPath.startsWith("/")
      ? `${basePath}${componentPath}`
      : `${basePath}/${componentPath}`;

    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    const html = await response.text();
    const element = document.getElementById(elementId);
    if (element) {
      element.innerHTML = html;
    } else {
      console.warn(`Ð­Ð»ÐµÐ¼ÐµÐ½Ñ‚ Ñ ID ${elementId} Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½`);
    }
  } catch (error) {
    console.error(`ÐžÑˆÐ¸Ð±ÐºÐ° Ð¿Ñ€Ð¸ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐµ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ð° ${componentPath}:`, error);
  }
}

export { loadComponent };
