// ÐœÐ¾Ð±Ð¸Ð»ÑŒÐ½Ð¾Ðµ Ð¼ÐµÐ½ÑŽ
export class MobileMenu {
  constructor() {
    // Ð­Ð»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ Ð¼ÐµÐ½ÑŽ
    this.sidebar = document.querySelector(".mobile-nav__sidebar");
    this.slides = document.querySelector(".mobile-nav__slides");
    this.mainSlide = document.querySelector(".mobile-nav__slide-main");
    this.submenuSlide = document.querySelector(".mobile-nav__slide-submenu");
    this.menuBtn = document.querySelector(".mobile-nav__menu-btn");
    this.closeBtn = document.querySelector(".mobile-nav__close");
    this.backBtn = document.querySelector(".mobile-nav__back");
    this.servicesBtn = document.querySelector(".mobile-nav__category-btn");
    this.categoriesList = document.querySelector(".mobile-nav__categories");
    this.subcategoryBtns = document.querySelectorAll(
      ".mobile-nav__subcategory-btn"
    );
    this.submenus = document.querySelectorAll(".mobile-nav__subsubmenu");

    // Ð¡Ð¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð¼ÐµÐ½ÑŽ
    this.isOpen = false;
    this.isSubmenuOpen = false;
    this.isCategoriesOpen = false;

    // ÐŸÑ€Ð¸Ð²ÑÐ·ÐºÐ° ÐºÐ¾Ð½Ñ‚ÐµÐºÑÑ‚Ð°
    this.toggleMenu = this.toggleMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.toggleCategories = this.toggleCategories.bind(this);
    this.showSubmenu = this.showSubmenu.bind(this);
    this.hideSubmenu = this.hideSubmenu.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);

    // Ð˜Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ¾Ð² ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ð¹
    this.initEventListeners();
  }

  // Ð˜Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ¾Ð² ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ð¹
  initEventListeners() {
    // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ/Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ
    this.menuBtn.addEventListener("click", this.toggleMenu);
    this.closeBtn.addEventListener("click", this.closeMenu);

    // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ/Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ ÑÐ¿Ð¸ÑÐºÐ° ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ð¸Ð¹
    this.servicesBtn.addEventListener("click", this.toggleCategories);

    // ÐÐ°Ð²Ð¸Ð³Ð°Ñ†Ð¸Ñ Ð¿Ð¾ Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ
    this.subcategoryBtns.forEach((btn) => {
      btn.addEventListener("click", () => {
        const category = btn.dataset.category;
        this.showSubmenu(category);
      });
    });

    this.backBtn.addEventListener("click", this.hideSubmenu);

    // Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ Ð¿Ñ€Ð¸ ÐºÐ»Ð¸ÐºÐµ Ð²Ð½Ðµ ÐµÐ³Ð¾
    document.addEventListener("click", this.handleClickOutside);

    // Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ Ð¿Ñ€Ð¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° Ð»ÑŽÐ±ÑƒÑŽ ÑÑÑ‹Ð»ÐºÑƒ Ð² Ð¼ÐµÐ½ÑŽ
    this.sidebar.addEventListener("click", (e) => {
      if (e.target.closest("a")) {
        this.closeMenu();
      }
    });

    // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÑ„Ñ„ÐµÐºÑ‚Ñ‹ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð¸Ð¸ Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸ Ð´Ð»Ñ Ð²ÑÐµÑ… Ð¸Ð½Ñ‚ÐµÑ€Ð°ÐºÑ‚Ð¸Ð²Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²
    this.addInteractiveEffects();
  }

  // Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¸Ð½Ñ‚ÐµÑ€Ð°ÐºÑ‚Ð¸Ð²Ð½Ñ‹Ñ… ÑÑ„Ñ„ÐµÐºÑ‚Ð¾Ð²
  addInteractiveEffects() {
    // ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ð²ÑÐµ Ð¸Ð½Ñ‚ÐµÑ€Ð°ÐºÑ‚Ð¸Ð²Ð½Ñ‹Ðµ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹
    const interactiveElements = this.sidebar.querySelectorAll("a, button");

    interactiveElements.forEach((element) => {
      // Ð­Ñ„Ñ„ÐµÐºÑ‚ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð¸Ð¸
      element.addEventListener("mouseenter", () => {
        element.style.transition = "all 0.2s ease";
        element.style.transform = "translateY(-1px)";
      });

      // Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‚ Ðº Ð¸ÑÑ…Ð¾Ð´Ð½Ð¾Ð¼Ñƒ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸ÑŽ
      element.addEventListener("mouseleave", () => {
        element.style.transform = "translateY(0)";
      });

      // Ð­Ñ„Ñ„ÐµÐºÑ‚ Ð¿Ñ€Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸
      element.addEventListener("mousedown", () => {
        element.style.transform = "translateY(1px)";
      });

      // Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‚ Ð¿Ð¾ÑÐ»Ðµ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ñ
      element.addEventListener("mouseup", () => {
        element.style.transform = "translateY(-1px)";
      });
    });
  }

  // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ/Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ
  toggleMenu() {
    if (this.isOpen) {
      this.closeMenu();
    } else {
      this.openMenu();
    }
  }

  // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ
  openMenu() {
    this.isOpen = true;
    this.sidebar.classList.add("is-open");
    document.body.style.overflow = "hidden";
  }

  // Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼ÐµÐ½ÑŽ
  closeMenu() {
    this.isOpen = false;
    this.isSubmenuOpen = false;
    this.isCategoriesOpen = false;
    this.sidebar.classList.remove("is-open");
    this.slides.classList.remove("show-submenu");
    this.servicesBtn.classList.remove("is-open");
    this.categoriesList.classList.remove("is-open");
    document.body.style.overflow = "";
  }

  // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ/Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ ÑÐ¿Ð¸ÑÐºÐ° ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ð¸Ð¹
  toggleCategories() {
    this.isCategoriesOpen = !this.isCategoriesOpen;
    this.servicesBtn.classList.toggle("is-open");
    this.categoriesList.classList.toggle("is-open");
  }

  // ÐŸÐ¾ÐºÐ°Ð·Ð°Ñ‚ÑŒ Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ Ð´Ð»Ñ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð¹ ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ð¸Ð¸
  showSubmenu(category) {
    this.isSubmenuOpen = true;
    this.slides.classList.add("show-submenu");

    // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ
    // const submenuTitle = this.submenuSlide.querySelector(
    //   ".mobile-nav__submenu-title"
    // );
    // const categoryBtn = document.querySelector(`[data-category="${category}"]`);
    // submenuTitle.textContent = categoryBtn.querySelector("span").textContent;

    // ÐŸÐ¾ÐºÐ°Ð·Ñ‹Ð²Ð°ÐµÐ¼ Ð½ÑƒÐ¶Ð½Ð¾Ðµ Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ
    this.submenus.forEach((submenu) => {
      if (submenu.dataset.category === category) {
        submenu.classList.add("is-active");
      } else {
        submenu.classList.remove("is-active");
      }
    });
  }

  // Ð¡ÐºÑ€Ñ‹Ñ‚ÑŒ Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ
  hideSubmenu() {
    this.isSubmenuOpen = false;
    this.slides.classList.remove("show-submenu");

    // Ð¡ÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼ Ð²ÑÐµ Ð¿Ð¾Ð´Ð¼ÐµÐ½ÑŽ
    this.submenus.forEach((submenu) => {
      submenu.classList.remove("is-active");
    });
  }

  // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚ÐºÐ° ÐºÐ»Ð¸ÐºÐ° Ð²Ð½Ðµ Ð¼ÐµÐ½ÑŽ
  handleClickOutside(event) {
    if (
      this.isOpen &&
      !this.sidebar.contains(event.target) &&
      !this.menuBtn.contains(event.target)
    ) {
      this.closeMenu();
    }
  }
}
