import homeVideoUrl from "@/assets/video/home.mp4";
// modalVideo.js

export function initVideoModal() {
    // ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ DOM Ñ Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ¾Ð¹ Ð½Ð° ÑÑƒÑ‰ÐµÑÑ‚Ð²Ð¾Ð²Ð°Ð½Ð¸Ðµ
    const videoBlock = document.getElementById('videoBlock');
    const playButton = document.getElementById('playButton');
    const modal = document.getElementById('videoModal');
    const video = document.getElementById('aboutVideo');
    const closeModalBtn = document.getElementById('closeModal');
    
    // Ð•ÑÐ»Ð¸ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ Ð½Ðµ ÑÑƒÑ‰ÐµÑÑ‚Ð²ÑƒÑŽÑ‚ Ð½Ð° ÑÑ‚Ð¾Ð¹ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ðµ - Ð²Ñ‹Ñ…Ð¾Ð´Ð¸Ð¼
    if (!videoBlock || !playButton || !modal || !video || !closeModalBtn) {
        return;
    }
    
    // ÐÐ°Ð·Ð½Ð°Ñ‡Ð°ÐµÐ¼ ÐºÐ¾Ñ€Ñ€ÐµÐºÑ‚Ð½Ñ‹Ð¹ URL Ð²Ð¸Ð´ÐµÐ¾ Ñ‡ÐµÑ€ÐµÐ· Vite, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ñ„Ð°Ð¹Ð» Ð¿Ð¾Ð¿Ð°Ð» Ð² ÑÐ±Ð¾Ñ€ÐºÑƒ
    try {
        if (homeVideoUrl) {
            video.src = homeVideoUrl;
            video.setAttribute('preload', 'none');
            // Ð’ÐÐ–ÐÐž: Ð½Ðµ ÑÑ‚Ð°Ð²Ð¸Ð¼ playsinline, Ñ‡Ñ‚Ð¾Ð± Ð½Ð° iOS Ð¿Ð¾Ð·Ð²Ð¾Ð»Ð¸Ñ‚ÑŒ Ð¿Ð¾Ð»Ð½Ð¾ÑÐºÑ€Ð°Ð½
        }
    } catch (e) {
        console.warn("ÐÐµ ÑƒÐ´Ð°Ð»Ð¾ÑÑŒ Ð½Ð°Ð·Ð½Ð°Ñ‡Ð¸Ñ‚ÑŒ src Ð´Ð»Ñ Ð²Ð¸Ð´ÐµÐ¾:", e);
    }

    // Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    async function openVideoModal() {
        modal.style.display = "block";
        document.body.style.overflow = "hidden";
        document.body.style.paddingRight = getScrollbarWidth() + 'px';

        // ÐŸÑ‹Ñ‚Ð°ÐµÐ¼ÑÑ Ð°ÐºÑ‚Ð¸Ð²Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ Ð¿Ð¾Ð»Ð½Ð¾ÑÐºÑ€Ð°Ð½Ð½Ñ‹Ð¹ Ñ€ÐµÐ¶Ð¸Ð¼ (ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸ Ð¸Ð»Ð¸ ÑÐ°Ð¼ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð²Ð¸Ð´ÐµÐ¾)
        const container = modal.querySelector('.video-container') || video;
        try {
            if (container.requestFullscreen) {
                await container.requestFullscreen();
            } else if (container.webkitRequestFullscreen) { // Safari desktop
                container.webkitRequestFullscreen();
            } else if (video.webkitEnterFullscreen) { // iOS Safari Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð´Ð»Ñ Ð²Ð¸Ð´ÐµÐ¾
                video.webkitEnterFullscreen();
            }
        } catch (e) {
            console.warn('Fullscreen error:', e);
        }

        // ÐÐ²Ñ‚Ð¾Ð²Ð¾ÑÐ¿Ñ€Ð¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ
        try {
            await video.play();
        } catch (e) {
            console.log("ÐÐ²Ñ‚Ð¾Ð²Ð¾ÑÐ¿Ñ€Ð¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ Ð½Ðµ Ñ€Ð°Ð·Ñ€ÐµÑˆÐµÐ½Ð¾:", e);
        }
    }
    
    // Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    async function closeVideoModal() {
        try { video.pause(); } catch(_) {}
        modal.style.display = "none";
        document.body.style.overflow = "auto";
        document.body.style.paddingRight = '';
        // Ð’Ñ‹Ñ…Ð¾Ð´Ð¸Ð¼ Ð¸Ð· Ð¿Ð¾Ð»Ð½Ð¾ÑÐºÑ€Ð°Ð½Ð½Ð¾Ð³Ð¾ Ñ€ÐµÐ¶Ð¸Ð¼Ð°, ÐµÑÐ»Ð¸ Ð¾Ð½ Ð°ÐºÑ‚Ð¸Ð²ÐµÐ½
        try {
            if (document.fullscreenElement && document.exitFullscreen) {
                await document.exitFullscreen();
            } else if (document.webkitFullscreenElement && document.webkitExitFullscreen) {
                document.webkitExitFullscreen();
            }
        } catch (e) {
            console.warn('Exit fullscreen error:', e);
        }
    }
    
    // Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ñ€Ð°ÑÑ‡ÐµÑ‚Ð° ÑˆÐ¸Ñ€Ð¸Ð½Ñ‹ scrollbar
    function getScrollbarWidth() {
        return window.innerWidth - document.documentElement.clientWidth;
    }
    
    // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»ï¿½ï¿½ ÐºÐ»Ð¸ÐºÐ° Ð¿Ð¾ Ð±Ð»Ð¾ÐºÑƒ Ð²Ð¸Ð´ÐµÐ¾
    videoBlock.addEventListener('click', function(e) {
        // ÐŸÑ€ÐµÐ´Ð¾Ñ‚Ð²Ñ€Ð°Ñ‰Ð°ÐµÐ¼ ÑÑ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°Ð½Ð¸Ðµ, ÐµÑÐ»Ð¸ ÐºÐ»Ð¸ÐºÐ½ÑƒÐ»Ð¸ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ (Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð½Ðµ Ð´ÑƒÐ±Ð»Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ)
        if (e.target !== playButton && !playButton.contains(e.target)) {
            openVideoModal();
        }
    });
    
    // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð²Ð¾ÑÐ¿Ñ€Ð¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð¸Ñ
    playButton.addEventListener('click', openVideoModal);
    
    // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ
    closeModalBtn.addEventListener('click', closeVideoModal);
    
    // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»Ñ ÐºÐ»Ð¸ÐºÐ° Ð²Ð½Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    modal.addEventListener('click', function(event) {
        if (event.target === modal) {
            closeVideoModal();
        }
    });
    
    // ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»Ñ ÐºÐ»Ð°Ð²Ð¸ÑˆÐ¸ ESC
    document.addEventListener('keydown', function(event) {
        if (event.key === "Escape" && modal.style.display === "block") {
            closeVideoModal();
        }
    });
    
    // ÐžÑÑ‚Ð°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð²ÑÐ¿Ð»Ñ‹Ñ‚Ð¸Ðµ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð´Ð»Ñ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð²Ð½ÑƒÑ‚Ñ€Ð¸ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    const modalContent = document.querySelector('.modal-content');
    if (modalContent) {
        modalContent.addEventListener('click', function(e) {
            e.stopPropagation();
        });
    }
    
    console.log('Modal video initialized successfully');
}